.stack {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}
.stack > * {
  margin-top: 0;
  margin-bottom: 3px;
}
.stack > * + * {
  margin-top: var(--s1);
}
.stack-tiny > * + * {
  margin-top: 0.1em;
}

.stack-small > * + * {
  margin-top: 0.5rem;
}

.stack-large > * + * {
  margin-top: 1.2rem;
}

.stack-extra-large > * + * {
  margin-top: 2rem;
}

.grid-row div[class*=bg], .grid-row section[class*=bg], .grid-row figure[class*=bg] {
  padding: var(--s3);
}

.left {
  margin-left: 0;
  margin-right: auto;
}

.right {
  margin-left: auto;
  margin-right: 0;
}

.center {
  margin-left: auto;
  margin-right: auto;
}

.rel-pos {
  position: relative;
}

.abs-pos {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

.text-centre {
  text-align: center;
  max-width: var(--measure);
  margin-left: auto;
  margin-right: auto;
}
.text-centre *:not(.button-wrap) {
  text-align: center;
  max-width: var(--measure);
  margin-left: auto;
  margin-right: auto;
}
.text-left {
  text-align: left;
}
.text-left * {
  text-align: left;
}
.text-right {
  text-align: right;
}
.text-right * {
  text-align: right;
}

.stretch.centreCentre {
  flex-direction: column;
  height: 100%;
  width: 100%;
  justify-content: center;
}

.stretch {
  display: flex;
  justify-self: stretch;
  align-self: stretch;
  flex-direction: column;
}
.stretch img {
  display: inherit;
  align-self: stretch;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.topLeft {
  display: flex;
  justify-self: flex-start;
  align-self: flex-start;
  flex-direction: column;
}

.topRight {
  display: flex;
  justify-self: flex-end;
  align-self: flex-start;
  flex-direction: column;
}

.topCentre {
  display: flex;
  justify-self: center;
  align-self: flex-start;
  flex-direction: column;
}

.centreLeft {
  display: flex;
  justify-self: flex-start;
  align-self: center;
  flex-direction: column;
}

.centreCentre {
  display: flex;
  justify-self: center;
  align-self: center;
  flex-direction: column;
}

.centreRight {
  display: flex;
  justify-self: flex-end;
  align-self: center;
  flex-direction: column;
}

.bottomLeft {
  display: flex;
  justify-self: flex-start;
  align-self: flex-end;
  flex-direction: column;
}

.bottomCentre {
  display: flex;
  justify-self: center;
  align-self: flex-end;
  flex-direction: column;
}

.bottomRight {
  display: flex;
  justify-self: flex-end;
  align-self: flex-end;
  flex-direction: column;
}